.main {
	display: flex;
	flex-wrap: nowrap;
	margin-top: 216px;
	@include PC-md{
		margin-top: 179px;
	}
	@include Pad{
		margin-top: 163px;
	}
	@include mobile-lg {
		margin-top: 138px;
	}
}

/*--------------------主選單開始------------------*/
.navbar {
	background: url("../images/header-bg.png") no-repeat;
	justify-content: center;
	background-size: cover;
	width: 100%;
	box-shadow: 0 3px 5px $gray-500;
	@include mobile-lg{
		background-size: 1200px;
	}
	&-brand {
		width: 320px;
		@include PC-md{
			width: 300px;
		}
		@include PC{
			width: 280px;
		}
		@include Pad{
			width: 180px;
		}
	}
	&-nav{
		background: $white;
		border-radius: 80px 0 0 80px;
		box-shadow:  3px 3px 5px $gray-600;
		@include Pad{
			border: none;
			background: none;
			box-shadow: none;
		}
		@include mobile-lg{
			width: 100%;
			display: flex;
			justify-content: flex-end;
		}
		.nav-item{
			display: flex;
			align-items: center;
			@include PC{
				font-size: $h7-font-size;
			}
			@include Pad{
				font-size: $h8-font-size;
				color: $white;
				img{
					filter: invert(100%) brightness(1200%) contrast(100%);
				}
			}
			&:first-child{
				padding-right: 16px;
				margin-right: 16px;
				border-right: 1px solid $gray-300;
			}
		}
		.nav-link{
			display: flex;
				align-items: center;
				color: $secondary;
				padding: 0;
				@include Pad{
					color: $primary-light;
				}
		}
		.nav-btn{
			background: $primary;
			&:hover{
				background: darken($color: $primary, $amount: 5%);
			}
			@include Pad{
				background: $secondary;
				&:hover {
						background: darken($color: $secondary, $amount: 5%);
					}
			}
		}
	}
	&-header{
		background: url("../images/header-bg.jpg") no-repeat;
		background-size: cover;
		color: $gray-800;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		height: 140px;
		padding: 0 96px;
		position: fixed;
		top: 75px;
		z-index: 1000;
		@include PC-md{
			top: 59px;
			height: 120px;
		}
		@include PC{
			padding: 0 8%;
		}
		@include Pad{
			top: 43px;
		}
		@include mobile-lg{
			top: 68px;
			height: 90px;
		}
		@include mobile-md{
			padding: 0 3%;
			height: 70px;
		}
		&-img{
			width: 370px;
			@include PC-md{
				width: 300px;
			}
			@include Pad{
				width: 55%;
			}
			@include mobile-md{
				width: 45%;
			}
		}
		&-title{
			font-weight: $font-weight-bold;
			@include PC-md{
				font-size: $h2-font-size;
			}
			@include PC{
				font-size: $h3-font-size;
			}
			@include Pad{
				font-size: $h4-font-size;
			}
			@include mobile-lg{
				font-size: $h5-font-size;
				margin-bottom: 0;
			}
		}
	}
}
/*--------------------側邊選單開始------------------*/
.side-menu {
	width: 345px;
	height: calc(100% - 216px);
	flex-shrink: 0;
	transition: all 0.2s ease;
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 150;
	background: $white;
	box-shadow: 0 3px 5px $gray-500;
	@include PC-md{
		height: calc(100% - 179px);
	}
	@include PC{
		width: 280px;
	}
	@include PC-sm {
		position: fixed;
	}
	@include Pad{
		height: calc(100% - 163px);
	}
	@include mobile-lg {
		height: calc(100% - 158px);
	}
	@include mobile-md{
		height: calc(100% - 138px);
	}
	&-header {
		font-size: $h5-font-size;
		color: $white;
		text-align: center;
		font-weight: $font-weight-semibold;
		padding: 24px 0 16px 0;
		margin-bottom: 0;
		border-bottom: 2px solid $white;
		@include PC {
			font-size: $h6-font-size;
		}
	}
	&-body{
		height: 100%;
	}
	&-item {
		background: none;
		border: none;
		border-radius: 0 !important;
	}
	&-accordion{
		background: $primary-light;
		li:first-child{
			.side-menu-link{
				padding-top: 16px;
			}
		}
		li:last-child {
			.side-menu-link{
				padding-bottom: 16px;
				&:after{
					display: none;
				}
			}
		}
	}
	&-link {
		background: none !important;
		display: flex;
		align-items: center;
		padding: 8px 16px 8px 56px;
		border-radius: 10px 0 0 10px;
		font-weight: $font-weight-medium;
		color: $gray-900 !important;
		border-bottom: 1px solid rgba($color: $primary, $alpha: .15);
		@include PC{
			padding-left: 24px;
		}
		&:hover {
			color: $primary;
		}
	}
	&-btn{
		background: none !important;
		box-shadow: none !important;
		border-radius: 0 !important;
		position: relative;
		font-size: $h5-font-size;
		justify-content: flex-start;
		padding-left: 56px !important;
		border-bottom: 1px solid $gray-400 !important;
		@include PC{
			padding-left: 24px !important;
			font-size: $h6-font-size;
		}
		&:after{
			background: url("../images/ic-dropdown.svg") center !important;
			transform: rotate(180deg);
			position: absolute;
			right: 24px;
			top: 24px;
			@include PC{
				right: 16px;
			}
		}
		img{
			width: 26px;
			@include PC{
				width: 20px;
			}
		}
	}
	&-btn:not(.collapsed){
		background: $primary !important;
		font-weight: $font-weight-semibold;
		color: $white;
		border-bottom: none;
		&:after{
			background: url("../images/ic-dropdown-white.svg") center !important;
			transform: rotate(0deg);
		}
		img{
			filter: brightness(800);
		}
	}
	&-toggler {
		background: $primary;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		color: $white !important;
		width: 32px;
		height: 124px;
		border-radius: 0 8px 8px 0 !important;
		position: absolute;
		right: -32px;
		top: 0px;
		line-height: 1.2;
		z-index: 10;
		transition: all 0.2s ease;
		border: none;
		@include mobile-lg{
			width: 28px;
			height: 110px;
			font-size: $h7-font-size;
			right: -28px;
		}
		&:hover {
			background: $primary-dark !important;
		}
		&:active,
		&:focus {
			background: $primary !important;
			outline: none !important;
		}
		img {
			width: 18px;
			margin-right: 0;
			transform: rotate(180deg);
		}
	}
	&.close {
		position: fixed;
		@include PC-sm {
			left: -280px;
		}
		.side-menu-toggler {
			img {
				transform: rotate(0deg);
			}
		}
	}
	&.fixed {
		position: fixed;
		top: 71px;
	}
}
.side-content {
	margin-left: 345px;
	width: 100%;
	@include PC {
		margin-left: 280px;
		width: calc(100% - 280px);
	}
	@include PC-sm {
		margin-left: 0;
		width: 100%;
	}
	&-header{
		display: flex;
		align-items: center;
		background: $gray-100;
		border-bottom: 1px solid $gray-200;
		padding: 8px calc(3% + 12px);
		@include Pad{
			flex-direction: column-reverse;
			align-items: flex-start;
		}
	}
	&-main{
		padding: 24px 3%;
	}
	&-title{
		margin-bottom: 0;
		font-weight: $font-weight-heavy;
		@include PC{
			font-size: $h4-font-size;
		}
		@include Pad{
			font-size: $h5-font-size;
		}
	}
	&-subtitle {
		color: $gray-800;
		font-weight: $font-weight-semibold;
		font-size: $h4-font-size;
		display: flex;
		align-items: center;
		@include PC{
			font-size: $h5-font-size;
		}
		@include Pad{
			font-size: $h6-font-size;
		}
		img{
			width: 50px;
			@include PC{
				width: 40px;
			}
		}
	}
}
.side-container {
	background: $white;
	border-radius: 10px;
	box-shadow: 0 3px 6px $gray-400;
}
.side-container-header {
	border-bottom: 2px solid $secondary-pale;
	z-index: 1;
	ul {
		display: flex;
	}
	li {
		width: 100%;
		@include PC-lg {
			width: auto;
			flex-shrink: 0;
		}
		&:first-child {
			a {
				background: none;
			}
		}
	}
	a {
		background: url("../images/ic-gonext.svg") no-repeat left center;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20px;
		background-size: 10px;
		width: 100%;
		font-weight: $font-weight-bold;
		color: $gray-800;
		position: relative;
		@include PC-lg {
			padding: 20px 24px;
		}
		img {
			margin-right: 8px;
			width: 20px;
		}
		&.active {
			color: $secondary-dark;
			&::after {
				content: "";
				width: 100%;
				height: 5px;
				left: 0;
				bottom: 0;
				background: $secondary-pale;
				position: absolute;
			}
			img {
				filter: invert(56%) sepia(29%) saturate(722%) hue-rotate(96deg) brightness(97%) contrast(92%);
			}
		}
	}
}
/*--------------------footer開始------------------*/
.footer {
	width: calc(100% - 340px);
	margin-left: 340px;
	padding: 16px 0;
	background: $gray-200;
	color: $gray-800;
	font-size: $h8-font-size;
	z-index: 100;
	position: relative;
	@include PC-lg{
		padding-left: 150px;
	}
	@include PC-md {
		padding-left: 16px;
	}
	@include PC{
		width: calc(100% - 280px);
		margin-left: 280px;
	}
	@include PC-sm{
		width: 100%;
		margin-left: 0;
	}
	&-logo{
		width: 130px;
		@include PC-lg{
			position: absolute;
			left: 0;
			top: calc(50% - 19px);
		}
		@include PC-md{
			left: 24px;
			top: 12px;
		}
	}
	&-inner {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include PC-lg{
			justify-content: flex-start;
		}
		@include PC-md{
			flex-direction: column;
			align-items: flex-start;
		}
		p{
			@include PC-lg{
				display: flex;
				flex-direction: column;
				font-size: $h8-font-size;
			}
			@include PC-md{
				flex-direction: row;
				flex-wrap: wrap;
			}
		}
	}
	&-gotop{
		display: block;
		position: fixed;
		right: 16px;
		bottom: 48px;
		width: 56px;
		height: 56px;
		border-radius: 50%;
		background: $gray-800;
		color: $white !important;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border: 2px solid $white;
		font-size: $h7-font-size;
		font-weight: $font-weight-medium;
		&:hover{
			background: $gray-700;
		}
		img{
			width: 16px;
		}
	}
	a{
		color: $primary;
	}
}


