/* ---------------------登入頁開始------------------------*/
.login {
    background: url("../images/login-bg.jpg") center;
    background-size: cover;
    min-height: calc(100vh - 46px);
    display: flex;
    flex-direction: column;
    @include PC-sm{
        min-height: calc(100vh - 110px);
        background: url("../images/login-bg-mobile.jpg") center;
        background-size: cover;
    }
    &-form {
        width: 90%;
        max-width: 600px;
        margin-right: 13%;
        min-height: calc(100vh - 46px);
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        @include PC-lg{
            width: 90%;
        max-width: 475px;
        margin-right: 10%;
        }
        @include PC{
            margin: 0 80px;
        }
        @include PC-sm{
            max-width: 400px;
            padding-top: 130px;
            margin: 0 auto;
            min-height: calc(100vh - 110px);
        }
        @include mobile-lg{
            padding-top: 160px;
        }
        input {
            padding: 16px 16px 16px 60px;
            border: 1px solid $primary;
            @include PC-lg{
                padding: 12px 16px 12px 60px;
            }
            &[data-type="account"]{
                background: url("../images/ic-account.svg") no-repeat 16px center $white;
            }
            &[data-type="password"]{
                background: url("../images/ic-password.svg") no-repeat 16px center $white;
            }
        }
        &-logo {
            width: 40%;
            max-width: 196px;
            margin-right: auto;
            margin-left: 48px;
            @include PC-lg{
                margin-left: 0;
            }
            @include PC-sm{
                margin-bottom: 32px;
            }
            @include Pad{
                width: 240px;
                margin-bottom: 16px;
            }
            @include mobile-lg {
                width: 200px;
            }
        }
        &-title {
            display: block;
            max-width: 417px;
            width: 80%;
            margin-bottom: 56px;
            @include PC-lg{
                font-size: $h1-font-size;
            }
            @include PC-sm {
                font-size: $h2-font-size;
                margin-bottom: 64px;
            }
            @include Pad {
                font-size: 1.8em;
                margin-bottom: 48px;
            }
            @include mobile-lg {
                font-size: 1.45em;
                letter-spacing: 0;
                & > br {
                    display: none;
                }
            }
        }
        &-btn{
            font-size: $h5-font-size;
            padding: 12px 0;
            @include PC-lg{
                padding: 9px 0;
            }
            @include Pad{
                font-size: $h6-font-size;
            }
        }
        &-link{
            display: flex;
            align-items: center;
            font-weight: $font-weight-medium;
            @include Pad{
                font-size: $h7-font-size;
                img{
                    width: 18px;
                }
            }
        }
    }
    &-footer{
        background: $primary;
        color: $white;
        padding: 12px 0;
        display: flex;
        justify-content: center;
        font-size: $h7-font-size;
        position: relative;
        @include PC-sm{
            font-size: $h7-font-size;
            flex-direction: column;
            padding: 12px 5%;
        }
        &:before {
            content:"";
            display: block;
            position: absolute;
            left: 0;
            top: -7px;
            width: 100%;
            height: 3px;
            background: $primary;
        }
        &-link{
            display: flex;
            align-items: center;
            color: $white;
            &:hover{
                color: $secondary-light;
            }
            img{
                width: 20px;
            }
        }
    }

}
